// reset some basic elements
body, h1, h2, h3, h4, h5, h6,
p, blockquote, pre, hr,
dl, dd, ol, ul, figure {
    margin: 0;
    padding: 0;
}

// Set the background colour for scrolling too far on chrome.
html {
    background-color: $background-secondary;
}

// Allow for IE by displaying main as a block.
main {
    display: block;
}

// Basic styling
body {
    font: $base-font-weight #{$base-font-size}/#{$base-line-height} $base-font-family;
    color: $text-main;
    background-color: $background-main;
    -webkit-text-size-adjust: 100%;
    -webkit-font-feature-settings: "kern" 1;
    -moz-font-feature-settings: "kern" 1;
    -o-font-feature-settings: "kern" 1;
    font-feature-settings: "kern" 1;
    font-kerning: normal;
}

// Set `margin-bottom` to maintain vertical rhythm
h1, h2, h3, h4, h5, h6,
p, blockquote, pre,
ul, ol, dl, figure,
%vertical-rhythm {
    margin-bottom: $spacing-unit / 2;
}

// Images
img {
    width: 100%;
}

// Figures
figure > img {
    display: block;
}

figcaption {
    font-size: $small-font-size;
}

// Lists
ul, ol {
    margin-left: $spacing-unit;
}

li {
    > ul,
    > ol {
         margin-bottom: 0;
    }
}

// Headings
h1, h2, h3, h4, h5, h6 {
    font-weight: $base-font-weight;
}

// Links
a {
    color: $theme-main;
    text-decoration: none;

    &:visited {
        color: $theme-main;
    }

    &:hover {
        color: $text-secondary !important;
        text-decoration: underline;
    }
}

// Blockquotes
blockquote {
    color: $borders;
    border-left: 4px solid $borders-light;
    padding-left: $spacing-unit / 2;
    font-size: 18px;
    letter-spacing: -1px;
    font-style: italic;

    > :last-child {
        margin-bottom: 0;
    }
}

// Code formatting
pre,
code {
    font-size: 15px;
    border: 1px solid $borders-light;
    border-radius: 3px;
    background-color: rgb(244, 242, 240);
}

code {
    padding: 1px 5px;
}

pre {
    padding: 8px 12px;
    overflow-x: auto;

    > code {
        border: 0;
        padding-right: 0;
        padding-left: 0;
    }
}

// Wrapper
.wrapper {
    max-width: $content-width - $spacing-unit * 2;
    margin-right: auto;
    margin-left: auto;
    padding-right: $spacing-unit;
    padding-left: $spacing-unit;
    @extend %clearfix;

    @include media-query($on-laptop) {
        max-width: $content-width - $spacing-unit;
        padding-right: $spacing-unit / 2;
        padding-left: $spacing-unit / 2;
    }
}

// Clearfix
%clearfix:after {
    content: "";
    display: table;
    clear: both;
}

// Icons
.icon > svg {
    display: inline-block;
    width: 16px;
    height: 16px;
    vertical-align: middle;

    path {
        fill: $text-secondary;
    }
}
